<script>
	import {
		mapState,
		mapGetters
	} from 'vuex'
	import {
		imConfig
	} from './util/config.js'

	export default {

		data() {
			return {

			}
		},
		computed: {
			...mapState({
				isSdkReady: state => state.global.isSdkReady,
				userInfo: state => state.userInfo,
				loginStatus: state => state.loginStatus
			})
		},

		// 应用生命周期仅可在App.vue中监听，在其它页面监听无效。
		onLaunch: function() {
			console.log("onlanch初始化")

			let that = this
			// #ifdef MP-WEIXIN
			// wx.setEnableDebug({
			// 	enableDebug: true
			// });
			if (uni.getStorageSync('token')) {
				if (uni.getStorageSync('userInfo')) {
					that.$store.dispatch('setLoginStatus', 1)
					that.$store.dispatch('setUserInfo', uni.getStorageSync('userInfo'))
					// } else {
					// that.$store.dispatch('setLoginStatus', 1)
					// that.$store.dispatch('setUserInfo', uni.getStorageSync('userInfo'))
				}
			} else {
				that.$store.dispatch('setLoginStatus', 0);
				that.$store.dispatch('logout');
			}

			that.$store.commit('restAllUnreadCount')
			console.log('loading login status.......', this.$store.state.loginStatus)


			if (wx.canIUse('getUpdateManager')) {
				const updateManager = wx.getUpdateManager()
				updateManager.onCheckForUpdate(function(res) {
					console.log('onCheckForUpdate====', res)
					// 请求完新版本信息的回调
					if (res.hasUpdate) {
						console.log('res.hasUpdate====')
						updateManager.onUpdateReady(function() {
							wx.showModal({
								title: '更新提示',
								content: '新版本已经准备好，是否重启应用？',
								success: function(res) {
									console.log('success====', res)
									// res: {errMsg: "showModal: ok", cancel: false, confirm: true}
									if (res.confirm) {
										// 新的版本已经下载好，调用 applyUpdate 应用新版本并重启
										updateManager.applyUpdate()
									}
								}
							})
						})
						updateManager.onUpdateFailed(function() {
							// 新的版本下载失败
							wx.showModal({
								title: '已经有新版本了哟~',
								content: '新版本已经上线啦~，请您删除当前小程序，重新搜索打开哟~'
							})
						})
					}
				})
			}
			// #endif

		},
		onShow() {
			console.log("onShow初始化嘻嘻")
			// console.log(this.isSdkReady)


			if (!this.isSdkReady) {
				if (this.loginStatus === 1) {
					// console.log("登录状态为1")
					this.imLogin(this.userInfo.userId)
				} else {
					this.checkLogin();
				}
			}
			this.getAllHouseType();

		},
		onHide() {
			// console.log("onHide")
		},
		methods: {
			/**
			 * 检查session 是否已经登录
			 */
			checkLogin() {
				let that = this;
				// uni.showLoading({title:"加载中"});
				uni.login({
					provider: "weixin",
					success: (loginRes) => {
						// uni.hideLoading();
						if (loginRes && loginRes.code) {
							that.$http.getSessionToken({
									code: loginRes.code
								})
								.then(res => {
									if (res && res.code == 200 && res.data && res.data.token) {
										let userInfo = {
											avatarUrl: res.data.avatarUrl,
											isSubscribe: res.data.isSubscribe,
											isService: res.data.isService,
											nickName: res.data.nickName,
											userId: res.data.userId
										};
										that.avatarUrl = userInfo.avatarUrl;
										that.nickName = userInfo.nickName;
										that.$store.commit('setToken', res.data.token);
										that.$store.commit('setUserInfo', userInfo);
										that.$store.commit('setLoginStatus', 1);
										that.imLogin(res.data.userId)
									}
								})
						}
					}
				})
			},

			/**
			 * 获取categorys
			 */
			// getCategory() {
			// 	if (!uni.getStorageSync("catalogues")) {
			// 		this.$http.getCategory({}).then(res => {
			// 			this.$store.commit('setCatalogues', res.data);
			// 		});
			// 	}

			// },
			// 获取全部户型
			getAllHouseType() {
				if (!uni.getStorageSync("allHouseType")) {
					this.$http.getAllHouseType({}).then(res => {
						this.$store.commit('setAllHouseType', res.data);
					});
				}
			},
			// im登录
			imLogin(id) {
				const self = this
				self.$http.tencentyunUserSig({
						id
					})
					.then(res => {
						const {
							userId,
							userSig
						} = res.data
						const promise = this.$tim.login({
							userID: userId,
							userSig
						})
						promise.then(function(imResponse) {
							console.log(imResponse.data) // 登录成功
						}).catch(function(imError) {
							console.warn('login error:', imError) // 登录失败的相关信息
						})
					})
					.catch(err => {
						console.log(err)
					})
			},


		}

	}
</script>

